.reviews {
    min-height: 70vh;
    background-image: linear-gradient(to right, rgba(0, 0, 0, .7), 70%, rgba(255, 255, 255, .6)), url('../assets/images/nature.jpg');
    background-size: cover;
    padding: 3em;
}

.flex-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.reviews h3 {
    background-image: linear-gradient(to right, rgb(6, 157, 82), rgb(9, 236, 122));
    color: transparent;
    display: inline-block;
    background-clip: text;
    font-size: 3rem;
}

.review {
    display: flex;
    flex-direction: column;
    background-color: rgba(255, 255, 255, .6);
    padding: 1em;
    border-radius: 20px;
    text-align: center;
    max-width: 400px;
    margin-top: 2em;
}

.review:hover {
    background-color: white;
}

.review:hover img {
    transform: scale(1.5);
}

.image {
    width: 10em;
    height: 10em;
    overflow: hidden;
    margin: 0 auto;
    border-radius: 50%;
}
.review img {
    height: 10em;
    width: 10em;
    margin: 2em;
    border-radius: 50%;
    margin: 0 auto 2em auto;
    transition: all 10s;
}

.review > * {
    margin-bottom: 2em;
}

.review p {
    width: 100%;
}
