.form {
    min-height: 80vh;
    display: flex;
    position: relative;
}

.form-step {
    display: none;
    opacity: 0;
    transition: all .5s;
}

.visible {
    display: block;
    opacity: 1;
}

.left .title {
    font-size: 2rem;
}

.left {
    background-image: linear-gradient(to right, rgba(6, 157, 82, .7), rgba(9, 236, 122, .6));
    padding: 5em;
    color: white;
    width: 100vw;
}

.container {
    display: flex;
    justify-content: space-around;
    width: 45vw;
}

.step {
    margin: 0 auto;
}

.right {
    position: absolute;
    right: 0;
    clip-path: polygon(10% 0, 100% 0, 100% 100%, 0% 100%);
    min-height: 80vh;
    width: 50vw;
    background-color: white;
    text-align: center;
}

.container {
    display: flex;
}

.step {
    display: flex;
    flex-direction: column;
    margin: 2em;
    text-align: center;
    color: grey;
}

.fa {
    font-size: 5rem;
}

.done {
    color: #ccc;
}

.active {
    color: white;
}

.radio-group {
    text-align: left;
    margin-left: 30%;
}

.radio {
    display: none;
}

.radio-label {
    display: block;
    cursor: pointer;
    padding-left: 4.5em;
    position: relative;
    padding: 2em;
    font-size: 1.5rem;
}

.radio-button {
    height: 3rem;
    width: 3rem;
    border: 5px solid rgb(9, 236, 122);
    border-radius: 50%;
    display: inline-block;
    position: relative;
}

.radio-button::after {
    content: "";
    display: block;
    height: 1.3rem;
    width: 1.3rem;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(9, 236, 122);
    opacity: 0;
    transition: opacity .2s;
}

.radio:checked ~ .radio-label > .radio-button::after {
    opacity: 1;
}

.location {
    position: relative;
    top: -20px;
    left: 10px;
}

.treename > :not(a){
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.treename > label {
    top: 40%;
    font-size: 3rem;
    background-image: linear-gradient(to right, rgba(6, 157, 82), rgba(9, 236, 122));
    color: transparent;
    background-clip: text;
} 

.treename > .error {
    left: 40%;
}

.error {
    opacity: 0;
}

.mistake {
    opacity: 1;
}

.next, .submit {
    position: absolute;
    right: 2em;
    bottom: 1em;
}

.back {
    position: absolute;
    left: 2em;
    bottom: 1em;
}

.next, .back, .submit {
    background-color: rgb(9, 236, 122);
    color: white;
    padding: 1em 2em;
    font-size: 1.5rem;
    border: none;
    border-radius: 50px;
    cursor: pointer;    
}

.next:hover {
    background-color: white;
    color: rgb(9, 236, 122);
    outline: 2px solid rgb(9, 236, 122);
}

.text {
    border: none;
    border-bottom: 2px solid rgb(9, 236, 122);
    font-size: 2em;
    outline: none;
}

.textone {
    width: 50%;
}

.textthree {
    width: 30%;
}

.label {
    display: block;
    margin-top: 3em;
}

.no-block {
    display: flex;
}

.congratulations {
    position: relative;
    top: 50%;
}

.congratulations h3 {
    display: inline-block;
    color: transparent;
    background-image: linear-gradient(to right, rgba(6, 157, 82), rgba(9, 236, 122));
    background-clip: text;
    font-size: 3em;
}

.congratulations p {
    font-size: 1.5em;
}

.check {
    font-size: 1.5rem;
}

.error {
    color: #D5484F;
}

.error-name {
    position: absolute;
    margin: 100px;
}

.error-card {
    position: absolute;
    bottom: 50px;
    left: 270px;
}

@media (max-width: 1200px) {
    .left {
        width: 100vw;
        padding: .5em;
    }
    .container {
        width: 100vw;
    }
    .right {
        clip-path: none;
        width: 100vw;
        position: relative;
    }
    .form {
        display: block;
    }
    label {
        width: 100%;
    }
    .next, .back, .submit {
        padding: .5em 1em;
        margin: -1em;
    }
    .treename {
        left: 20px;
    }
    #treename {
        width: 70vw;
        top: 100px;
    }
    .fa {
        font-size: 2em;
    }
    .radio-label {
        padding: 2em 0;
    }
    #treename {
        left: 50%;
    }
    .congratulations {
        margin: 2em;
        top: 50px;
    }
    .congratulations h3 {
        font-size: 2rem;
    }
    .error-name {
        margin: 50px 30px;
    }
    .error-card {
        left: 80px;
        bottom: 80px;
    }
}